import React, { Component }  from 'react';
import { 
  ListGroup, 
  ListGroupItem,
  Button  } from 'reactstrap';
import { observer, inject } from 'mobx-react';

@inject("store")
@observer
class MyResume extends Component 
{
  componentDidMount()
  {
    this.props.store.get_my_resume();
  }

  async delete_confirm ( resume_id )
  {
      // alert(resume_id);
      if ( window.confirm("") )
      {
        const data = await this.props.store.delete(resume_id);
        if(data.code === 0)
          window.location.reload()
        else
          alert(data.err);
      }
  }
  
  render()
  {
    const resume_list = this.props.store.my_resume_list;
    return (
      <div>
          <h1></h1>
          <ListGroup>
            { resume_list.length > 0 && resume_list.map( (item)=>{
              // item
            return <ListGroupItem className="d-flex justify-content-between align-items-center" key={item.id} >
              <Button className="mr-auto" color="light" href={'/resume/'+item.id} target="_blank" key={item.id} >{item.title}</Button>
              <a href={'/resume/'+item.id} target="_blank"><img src="open_in_new.png" alt=""/></a>
              <a href={'/resume_modify/'+item.id} ><img className="resume_modify" src="mode_edit.png" alt=""/></a>
              <a onClick={(e)=>{this.delete_confirm(item.id);}} ><img className="resume_delete" src="close.png" alt=""/></a>
            </ListGroupItem>
            } ) }

            { resume_list.length === 0 && 
            <Button color="light" href="/resume_add" className="resume_add">
              <img src="add.png" alt="" />
            </Button> }
          </ListGroup>

            { resume_list.length > 0 && 
            <Button color="light" href="/resume_add" className="resume_add">
              <img src="add.png" alt="" />
            </Button> }
      </div>
    );
  }
}

export default MyResume;



