import React, { Component }  from 'react';
import { 
    Collapse, 
    Navbar,
    NavbarToggler, 
    NavbarBrand, 
    Nav, 
    NavItem, 
    NavLink,} from 'reactstrap';
import { observer , inject } from 'mobx-react';

@inject("store")
@observer
class Header extends Component 
{
    constructor(props) 
    {
      super(props);
      this.toggleNavbar = this.toggleNavbar.bind(this);
      this.state = { collapsed: true };
    }
  
    toggleNavbar() {
      // console.log("clicked");
      this.setState({collapsed: !this.state.collapsed});
    }
    
    render()
    {
      return (
        <div>
            <Navbar style={{'backgroundColor':'#eee'}}  light >
                {/* bootstrap */}
                <NavbarBrand href="/"  className="mr-auto header-logo">
                    <img src="czx.ico" height="45" className="d-inline-block align-top" alt="logo" />
                    <p className="d-inline-block align-middle logo_text" ></p> 
                </NavbarBrand>

                <NavbarToggler className="navbar-toggler" onClick={this.toggleNavbar} />

                <Collapse isOpen={!this.state.collapsed} navbar>
                { this.props.store.token.length > 0 && <Nav className="ml-auto float-right" navbar>
                    <NavItem> <NavLink href="myresume"></NavLink></NavItem>
                    <NavItem> <NavLink href="logout"></NavLink></NavItem></Nav> }
                    
                { this.props.store.token.length === 0 && <Nav className="ml-auto float-right" navbar>
                    <NavItem> <NavLink href="reg"></NavLink></NavItem>
                    <NavItem> <NavLink href="login"></NavLink></NavItem></Nav>}
                </Collapse>
            </Navbar>
        </div>
      );
    }
  }
  
  export default Header;


