import React, { Component }  from 'react';
import { Button, Form, FormGroup, Label, Input} from 'reactstrap';
import { observer, inject } from 'mobx-react';
import { Redirect } from 'react-router'

@inject("store")
@observer
class Login extends Component 
{
  constructor(props) 
  {
    super(props);
    this.state = {'email': '','password':'',"redir":false};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  // 
  handleChange(event,field) {
    let data = {};
    data[field] = event.target.value;
    this.setState(data);
  }

  async handleSubmit(event) {
    const data = await this.props.store.login(this.state.email,this.state.password);
    console.log(data);
    
    if( data.code === 0 )
    {
      this.setState({'redir':true});
    }
    else
    {
      alert(data.err);
    }
  }

  render()
  {
    return (
      <div>
        <h1></h1>
        <Form onSubmit={this.handleSubmit}>
          <FormGroup>
            <Label for="exampleEmail">Email</Label>
            <Input type="email" name="email" className="form-control" placeholder="" 
            value={this.state.email} onChange={(e)=>{this.handleChange(e,'email');}}/>
          </FormGroup>
          <FormGroup>
            <Label for="examplePassword">Password</Label>
            <Input type="password" name="password"  className="form-control"  placeholder="(6~12)" 
            value={this.state.password} onChange={(e)=>{this.handleChange(e,'password');}} />
          </FormGroup>
          <Button color="primary" id="user_login" onClick={this.handleSubmit} ></Button>
          {/*  */}
          { this.props.store.token && <Redirect to="/myresume"/>}
        </Form>
      </div>
    );
  }
}

export default Login;



